@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Montserrat;
    font-size: 20px;
    background-color: #1a1a1c;
    color: #fff;
    
  }
  .player{
      transform: translateX(20%);
      margin: 20px;
      align-items: center;
      border: #fff .5px solid;
      width: 1000px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;
      max-width: 100%;
  }

  .player_name h1{
    margin-left:20px;
    text-align: start;
    width: 320px;
    font-size: 30px;
  }
  .player_kd h1{
    color:aquamarine;
    width:115px;
}
.solo{
    margin:10px;
}
.duo{
    margin:10px;
}
.trio{
    margin:10px;
}
.squad{
    margin:10px
}
.player h2{
    color:gold;
}
.play_time{
    padding-left: 20px;
}